<script>
import axios from "axios";
export default {
	name: "header",
	data() {
		return {
			lastUpdate: "???",
			isMobile: true,
			teachers: [],
			isStudent: true,
		};
	},
	props: {
		msg: String,
	},

	beforeUpdate() {
		document.title = "Rozklad";
	},
	beforeMount() {
		document.title = "Rozklad";
	},
	mounted: function() {
		axios

			.get("https://ap-rozklad-back.herokuapp.com/timetables/3/getInfo")
			// .get("http://localhost:5000/timetables/3/getInfo")
			// .get("https://fit-backend.ew.r.appspot.com/timetables/3/getInfo")
			.then(
				// rightNow.toISOString().slice(0,16).replace(/-/g,".").replace("T"," ");
				(response) => {
					this.lastUpdate = new Date(response.data.date)
						.toISOString()
						.slice(0, 16)
						.replace(/-/g, ".")
						.replace("T", " ");
					this.teachers = response.data.teachers;
				}
			);
		if (
			/Android|webOS|iPhone|iPad|iPod|BlackBerry|BB|PlayBook|IEMobile|Windows Phone|Kindle|Silk|Opera Mini/i.test(
				navigator.userAgent
			)
		) {
			//mobile
			this.isMobile = true;
			// console.log("mobile");
		} else {
			this.isMobile = false;
			//pc
		}
		setTimeout(() => {
			let elAr = document.getElementsByClassName("course");
			let elArMobile = document.getElementsByClassName("courseMobile");
			elAr.forEach((element) => {
				element.classList.add("courseEnd");
			});
			elArMobile.forEach((element) => {
				element.classList.add("courseEndMobile");
			});
			setTimeout(() => {
				let elAr = document.getElementsByClassName("course");
				let elAr2 = document.getElementsByTagName("a");

				[...elAr2, ...elAr].forEach((element) => {
					element.classList.add("courseEnd2");
				});
				// elAr2.forEach((element) => {
				//   element.classList.add("courseEnd2");
				// });
			}, 200);
		}, 100);
	},
};
</script>

